#about{

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    h1{
        font-family: 'Gugi', cursive;
        font-size: 1.8em;
        margin-bottom: 30px;
        
    }

    .about{
        width: 70%;
        margin-top: 90px;

        h1{
            white-space: nowrap;
        }

        h2{
            font-size: 1.3em;
            font-family: 'Montserrat', sans-serif;
            line-height: 1.3em;
        }

        p{
            font-family: 'Montserrat', sans-serif;
            line-height: 1.9em;
            font-size: 0.7em;
            letter-spacing: 0.09em;
            font-weight: 300;
            padding-bottom: 20px;
             //tablet and desktop
             @media screen and (min-width: 768px){
                margin-top: 30px;
                padding-top: 0;
                font-size: 0.9em;
                margin: 0 12%;
            }
            @media screen and (min-width: 1024px){
                margin: 0 14%;
            }
        }

        iframe{
            @media screen and (min-width: 768px){
                width: 560px; 
                height: 315px;   
            }
            @media screen and (min-width: 1024px){
                width: 690px; 
                height: 435px;   
            }
        }
    }

    .about5{
        img{
            width: 100%;
            @media screen and (min-width: 1024px) {
                width: 75%;
            }
        }
    }
}